.wrapper {
	margin-top: 15px;
	margin-right: 15px;
	margin-left: 7px;
	margin-bottom: 8px;
}

.avatar {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	-ms-border-radius: 50%;
	-o-border-radius: 50%;
	z-index: 2;
}

.name {
	color: #fff;
	text-decoration: none;
	padding: 5px;
	cursor: pointer;
	z-index: 1;
	margin: 0;
	font-size: 15px;
}

.name:hover {
	text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #49ff18, 0 0 30px #49ff18, 0 0 40px #49ff18,
		0 0 55px #49ff18, 0 0 75px #49ff18, 2px 2px 3px rgba(22, 186, 206, 0);
	transition: 0.2s ease-in;
	-webkit-transition: 0.2s ease-in;
	-moz-transition: 0.2s ease-in;
	-ms-transition: 0.2s ease-in;
	-o-transition: 0.2s ease-in;
}
.name.active {
	text-shadow: 0 0 20px #fff, 0 0 30px #ff4da6, 0 0 40px #ff4da6, 0 0 50px #ff4da6, 0 0 60px #ff4da6, 0 0 70px #ff4da6,
		0 0 110px #ff4da6;
	opacity: 1;
}

@media (max-width: 991px) {
	.name {
		font-size: 12px;
	}
	.avatar {
		width: 40px;
		height: 40px;
	}
}

